oi-select .select-dropdown ,
.oi-select .select-dropdown {
  position: absolute;
  width: inherit;
  overflow-y: scroll;
  max-height: 300px;
  min-width: 160px;
  font-size: 14px;
  background-color: #fff;
  border-radius: 0 0 4px 4px;
  border: 1px solid #66afe9;
  border-top: 0;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.075), 0 0 8px rgba(102,175,233,0.6);
  background-clip: padding-box;
  z-index: 1000;
}
