.oi-select:not(.multiple):not(.cleanMode) .select-search:after {
  content: "";
  position: absolute;
  display: block;
  right: 10px;
  width: 0;
  height: 0;
  margin-top: -19px;
  border-color: #000 transparent transparent transparent;
  border-style: solid;
